import React from 'react';
import './App.css';
import GameList from './GameList.js'
import CardEditor from './CardControls.js'

function App() {
  return (
    <div className="app">
      {
        // Disabling Card editor
        (false) &&
          <CardEditor/>
      }
      <GameList/>
    </div>
  );
}

export default App;
