import React from 'react';
import './App.css';

function Game(props) {

  const optionalLink = (linkText, url) => {
    if (!url) 
      return;
    return <a href={url} target="_blank" rel="noopener noreferrer">{linkText}</a>
  }
  return (
    <div className="game">
      <div className="thumbnail" style={{backgroundImage: `url(${props.image})`}}></div>
      <div className="content">
        <div className="title">{props.title}</div>
        {optionalLink("See on BoardGameGeek", props.bgg)}
        {optionalLink("Buy It Here", props.buy)}
        {optionalLink("Publisher Information", props.publisher)}
        {optionalLink("Pitch Video", props.pitchVideo)}
        {optionalLink("How To Play Video", props.howToPlayVideo)}
        {optionalLink("Rulebook", props.rulebook)}
        {optionalLink("Sell Sheet", props.sellSheet)}
      </div>
    </div>
  );
}

export default Game;
