import React from 'react';
import './CardControls.css';

const Styles = {
  propertyGrid: {
    gridColumn: 2,
    display: "grid",
    gridColumnGap: "2px",
    gridAutoFlow: "column",
    gridTemplateColumns: "auto, auto"
  },

  header: {
    fontSize: "smaller",
    borderTop: "solid 1px",
    backgroundColor: "rgba(1.0, 1.0, 1.0, 0.2)",
    marginTop: "6px",
    textTransform: "uppercase",
    textAlign: "center",
    gridColumn: "span 2"
  },
}

const Select = (props) =>
  <>
    <label>{props.label}</label>
    <select
      onChange={(event) => props.onChange(event.target.selectedOptions[0].value)}
      value={props.value}>
      { props.options.map((option) => (
        <option key={option}>{option}</option>
      ))}
    </select>
  </>

const GridPositionSelect = (props) => {

  const onSelectStartChange = (event) => {
    let selection = event.target.selectedOptions[0].value;
    props.onChange(selection, props.endValue);
  };

  const onSelectEndChange = (event) => {
    let selection = event.target.selectedOptions[0].value;
    props.onChange(props.startValue, selection);
  };

  return (
    <>
      <label>{props.label}</label>
      <div style={Styles.propertyGrid}>
        <select onChange={onSelectStartChange} value={props.startValue}>
        { props.startOptions.map((option) => (
          <option key={option}>{option}</option>
        ))}
        </select>
        <select onChange={onSelectEndChange} value={props.endValue}>
        { props.endOptions.map((option) => (
          <option key={option}>{option}</option>
        ))}
        </select>
      </div>
    </>
  );
};

const ColorPicker = (props) =>
  <>
    <label>{props.label}</label>
    <input
      type="color"
      value={props.value}
      onChange={(event) => props.onChange(event.target.value)}></input>
  </>

const TextBox = (props) =>
  <>
    <label>{props.label}</label>
    <input
      value={props.value}
      onChange={(event) => props.onChange(event.target.value)}></input>
  </>

const NumberBox = (props) => 
  <>
    <label>{props.label}</label>
    <input
      type="number"
      value={props.value}
      onChange={(event) => props.onChange(event.target.value)}></input>
  </>

const Slider = (props) => 
  <>
    <label>{props.label}</label>
    <input
      type="range"
      min="0"
      max="1"
      step="0.1"
      value={props.value}
      onChange={(event) => props.onChange(event.target.value)}></input>
  </>

const optionalValue = (value, defaultValue) => {
  return (value ? value : defaultValue);
}

let elementDefaultValues = {
  textContent: undefined,
  textAlign: "center",
  fontFamily: undefined,
  fontSize: 24,
  horizontalAlignment: "stretch",
  verticalAlignment: "stretch",
  textColor: "#000000",
  borderWidth: 0,
  borderRadius: 0,
  borderColor: "#000000",
  backgroundColor: "transparent",
  backgroundOpacity: 1.0,
  opacity: 1.0,
  gridColumnStart: "leftSafe",
  gridColumnEnd: "initial",
  gridRowStart: "topSafe",
  gridRowEnd: "initial",
}

const CardElement = (props) => {
  const propsOrDefault = (propertyName) => {
    let propsValue = props.element[propertyName];
    if (propsValue !== undefined) {
      return propsValue;
    }
    return elementDefaultValues[propertyName];
  }

  const toRGBA = (colorString, opacity) => {
    let regex = /#(..)(..)(..)/;
    let matches = colorString.match(regex);
    if (matches) {
      let rgb = matches.slice(1);
      let [r, g, b] = [...rgb.map(component => parseInt(component, 16))];
      let a = (opacity === undefined ? 1.0 : opacity);
      return `rgba(${r},${g},${b},${a})`;
    }
    return colorString;
  }

  return (
    <div 
      onClick={props.onClick}
      style={{
        justifySelf: propsOrDefault("horizontalAlignment"),
        alignSelf: propsOrDefault("verticalAlignment"),
        color: propsOrDefault("textColor"),
        borderColor: propsOrDefault("borderColor"),
        backgroundColor: toRGBA(propsOrDefault("backgroundColor"), propsOrDefault("backgroundOpacity")),
        borderStyle: "solid",
        borderWidth: propsOrDefault("borderWidth") + "px",
        borderRadius: propsOrDefault("borderRadius") + "px",
        opacity: propsOrDefault("opacity"),
        gridColumnStart: propsOrDefault("gridColumnStart"),
        gridColumnEnd: propsOrDefault("gridColumnEnd"),
        gridRowStart: propsOrDefault("gridRowStart"),
        gridRowEnd: propsOrDefault("gridRowEnd"),
        fontFamily: propsOrDefault("fontFamily"),
        fontSize: propsOrDefault("fontSize") + "px",
        textAlign: propsOrDefault("textAlign"),
      }}>{props.element.textContent}</div>
  );
}

class CardControls extends React.Component {

  constructor(props) {
    super(props);
    this.state = {};

    // Grab the props value, or if not present, the default
    for (let propertyName in elementDefaultValues) {
      this.state[propertyName] = optionalValue(props.element[propertyName], elementDefaultValues[propertyName]);
    }
  }

  setStateAndNotify(newState) {
    this.setState(newState);
    this.props.onUpdated({...this.state, ...newState});
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    // If props changed we need to override our local state
    if (this.props) {
      for (let propertyName in this.props.element) {
        if (this.props.element[propertyName] !== this.state[propertyName]) {
          console.log("update " + propertyName);
          this.setState({[propertyName]: this.props.element[propertyName]})
        }
      }
    }
  }

  render() {
    return (
      <div className="content">

        <label style={Styles.header}>Text</label>
        <TextBox label="Text"
          value={this.state.textContent}
          onChange={(value) => this.setStateAndNotify({textContent: value })}/>
        <TextBox label="Font Family"
          value={this.state.fontFamily}
          onChange={(value) => this.setStateAndNotify({fontFamily: value })}/>
        <NumberBox
          label="Font Size"
          value={this.state.fontSize}
          onChange={(value) => this.setStateAndNotify({fontSize: value})}/>
        <ColorPicker label="Text Color"
          value={this.state.textColor}
          onChange={(value) => this.setStateAndNotify({textColor: value })}/>
        <Select label="Text Alignment"
          value={this.state.textAlign}
          options={["start", "center", "end"]}
          onChange={(value) => this.setStateAndNotify({textAlign: value })}/>

        <label style={Styles.header}>Background</label>
        <ColorPicker label="Background Color"
          value={this.state.backgroundColor}
          onChange={(value) => this.setStateAndNotify({backgroundColor: value })}/>
        <Slider
          label=""
          value={this.state.backgroundOpacity}
          onChange={(value) => this.setStateAndNotify({backgroundOpacity: value})}/>

        <label style={Styles.header}>Border</label>
        <NumberBox
          label="Border Width"
          value={this.state.borderWidth}
          onChange={(value) => this.setStateAndNotify({borderWidth: value})}/>
        <NumberBox
          label="Border Radius"
          value={this.state.borderRadius}
          onChange={(value) => this.setStateAndNotify({borderRadius: value})}/>
        <ColorPicker label="Border Color"
          value={this.state.borderColor}
          onChange={(value) => this.setStateAndNotify({borderColor: value })}/>

        <label style={Styles.header}>Grid</label>
        <GridPositionSelect label="Column"
          startValue={this.state.gridColumnStart}
          endValue={this.state.gridColumnEnd}
          startOptions={["left", "leftSafe", "rightSafe", "right"]}
          endOptions={["left", "leftSafe", "rightSafe", "right", "initial"]}
          onChange={(start, end) => {
            this.setStateAndNotify({
              gridColumnStart: start,
              gridColumnEnd: end});
          }}/>
        <Select label=""
          value={this.state.horizontalAlignment}
          options={["start", "center", "end", "stretch"]}
          onChange={(value) => this.setStateAndNotify({horizontalAlignment: value })}/>
        <GridPositionSelect label="Row"
          startValue={this.state.gridRowStart}
          endValue={this.state.gridRowEnd}
          startOptions={["top", "topSafe", "bottomSafe", "bottom"]}
          endOptions={["top", "topSafe", "bottomSafe", "bottom", "initial"]}
          onChange={(start, end) => {
            this.setStateAndNotify({
              gridRowStart: start,
              gridRowEnd: end});
          }}/>
        <Select label=""
          value={this.state.verticalAlignment}
          options={["start", "center", "end", "stretch"]}
          onChange={(value) => this.setStateAndNotify({verticalAlignment: value })}/>

        <label style={Styles.header}>General</label>
        <Slider
          label="Opacity"
          value={this.state.opacity}
          onChange={(value) => this.setStateAndNotify({opacity: value})}/>
        
      </div>
    );
  }
}

let elements = [{
    backgroundColor: "#FF0000",
    backgroundOpacity: 0.5,
    gridColumnStart: "left",
    gridColumnEnd: "right",
    gridRowEnd: "bottom",
    gridRowStart: "top",
  },
  {
    backgroundColor: "#00FF00",
    backgroundOpacity: 0.5,
  },
  {
    textContent: "title",
    color: "#FFFFFF",
    backgroundColor: "#0000FF",
    backgroundOpacity: 0.25,
    verticalAlignment: "start",
  }
];

class CardEditor  extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      elements: elements,
      selectedElementIndex: 1
    };
  }
  
  render() {
    const updateElement = (element) => {
      let newElements = [...this.state.elements];
      newElements[this.state.selectedElementIndex] = {...element};
      this.setState({elements: newElements});
    }

    const addElement = () => {
      let element = {};
      let newElements = [...this.state.elements, element];
      this.setState({
        elements: newElements,
        selectedElementIndex: newElements.length - 1,
      });
    }

    const deleteElement = (element) => {
      let newElements = this.state.elements.filter((value) => value !== element);
      this.setState({
        elements: newElements,
        selectedElementIndex: newElements.length - 1,
      });
    }

    const deleteCurrentElement = () => {
      deleteElement(this.state.elements[this.state.selectedElementIndex]);
    }

    const importElements = () => {
      let link = document.querySelector("#uploadLink");
      link.click();
    }

    const importElementsFromFile = (file) => {
      let reader = new FileReader();
      reader.onload = (event) => {
        let result = JSON.parse(event.target.result);
        this.setState({
          elements: result
        })
      };
      reader.readAsText(file);
    }

    const exportElements = () => {
      let link = document.querySelector("#downloadLink");
      let blob = new Blob([JSON.stringify(this.state.elements, null, 2)], {type : 'application/json'});
      let downloadUrl = URL.createObjectURL(blob);
      link.download = "Export.json";
      link.href = downloadUrl;
      link.click();
      URL.revokeObjectURL(downloadUrl);
    }

    return (
      <div className="cardEditor"
        style={{
          display: "grid",
          gridTemplateColumns: "1fr 1fr",
          gridColumnGap: "20px"
        }}>
        <div className="card"
          style={{
            gridColumn: "1",
            display: "grid",
            gridTemplateColumns: "[left] 40px [leftSafe] 1fr [rightSafe] 40px [right]",
            gridTemplateRows: "[top] 40px [topSafe] 1fr [bottomSafe] 40px [bottom]"
          }}>
          { this.state.elements.map((element, index) => (
              <CardElement
                element={element}
                key={index}
                onClick={() => this.setState({selectedElementIndex: index})}/>
          ))}
        </div>
        <div
          style={{
            gridColumn: "2",
            display: "flex",
            flexDirection: "column"
          }}>
          <input
            type="range"
            min="0"
            max={this.state.elements.length - 1}
            value={this.state.selectedElementIndex}
            onChange={(event) => this.setState({selectedElementIndex: event.target.value})}/>
          <label>Element {parseInt(this.state.selectedElementIndex) + 1} of {this.state.elements.length}</label>
          <button onClick={addElement}>+</button>
          <button onClick={deleteCurrentElement}>-</button>
          <button onClick={importElements}>import</button>
          <input id="uploadLink" style={{display: "none"}} type="file" accept=".json" onChange={(event) => importElementsFromFile(event.target.files[0])}></input>
          <button onClick={exportElements}>export</button>
          <a id="downloadLink" style={{display: "none"}}>download</a>
          <CardControls
            element={this.state.elements[this.state.selectedElementIndex]}
            onUpdated={updateElement}/>
        </div>
      </div>
    );
  }
}

export default CardEditor;
